
 .tab-pages {    
    .tabs-menu {
        padding: 10px 20px 10px 0;
        margin: 0;
        width: 90%;
        ul {
            list-style-type: none;
            font-size: 1em;
            li {
                padding: 0;
                border-bottom: 1px solid rgba($tertiary-color, .8);
                a {
                    display: block;
                    width: 100%;
                    padding: 10px 0 10px 0;
                }
                a:hover {
                    text-decoration: none;
                    color: $tertiary-color;
                }
            }

            li.active a {
                font-weight: bold;
                position: relative;

                &::after {
                    content: ">";
                    position: absolute;
                    margin-left: 5px;
                    padding-right: 3px;
                    right: 0;
                }
            }
        }
    }

    .tabs-content {
        .content-page {
            scroll-margin-top: 4rem;
        }
    }

}

@media #{$desktop} {
    .tab-pages {
        display: flex;

        .tabs-menu {
            width: 28%;
            min-width: 28%;
            position: -webkit-sticky;
            position: sticky;
            top: 3.5rem;
            align-self: flex-start;
        }

        .tabs-content {
            max-width: 72%;
            border-left: 1px solid $tertiary-color;
            padding-left: 20px;
        }
    }
}